html {
  --p-header: 50px;
  --active-color: #00acdc;
}

body, li, ul, a {
  color: #000;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  text-decoration: none;
}

.header {
  padding: 0 var(--p-header);
  width: calc(100% - (var(--p-header) * 2));
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav__menu {
  list-style: none;
  display: flex;
}

.nav__menu__item:not(:last-child) {
  margin-right: 20px;
  position: relative;
}

.nav__menu__item a {
  text-transform: uppercase;
  white-space: nowrap;
  align-items: center;
  height: 60px;
  font-weight: 500;
  display: flex;
}

.is-active {
  color: var(--active-color);
}

.is-active:after {
  content: "";
  border-bottom: 4px solid var(--active-color);
  border-radius: 8px;
  width: 37px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__menu__item a:hover {
  color: var(--active-color);
}
